@import '../templates.scss';
@import '../variables.scss';

.wallets {
  display: flex;
  padding: 0 15px;
  margin: 0 auto;
  margin-bottom: 190px;
  max-width: 1324px;
  @media screen and (max-width: 1200px) {
    flex-direction: column-reverse;
    img {
      display: none;
    }
  }
  @media screen and (max-width: 800px) {
    margin-bottom: 115px;
    .img {
      display: block;
    }
  }
  &__content {
    max-width: 750px;
    width: 100%;
    margin-left: 65px;
    @media screen and (max-width: 800px) {
      margin-left: 0;
    }
    .name {
      @include fs48;
      margin: 20px 0 50px 0;
      font-weight: 500;
      font-family: 'Rubik', sans-serif;
    }
    .btns {
      display: flex;
      justify-content: space-between;
      margin-bottom: 50px;
      @media screen and (max-width: 800px) {
        margin-bottom: 20px;
        flex-wrap: wrap;
      }
      @media screen and (max-width: 340px) {
        justify-content: center;
      }
      .btn {
        @include btn;
        box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.25);
        color: #090F3F;
        @media screen and (max-width: 800px) {
          margin-bottom: 30px;
        }
      }
      .btn--yellow {
        background: $yellowBg;
        color: $lightBg;
      }
    }
    .cards {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      @media screen and (max-width: 800px) {
        flex-direction: column;
        align-items: center;
      }
      .card {
        display: flex;
        margin-bottom: 20px;
        padding: 18px;
        width: 350px;
        box-shadow: 0px 0px 35px rgba(0, 0, 0, 0.15);
        .logo {
          margin-right: 28px;
          width: 40px;
          height: 40px;
        }
        &__right {
          .card__title {
            @include fs14;
            margin-bottom: 10px;
            font-weight: 600;
            color: $blueBg;
          }
          .card__text {
            @include fs12;
            color: $grayBg;
          }
        }
      }
    }
  }
}